import * as React from 'react';
import { Link } from 'react-router-dom';
import { useGlobalState } from 'piral-core';
import { PiralDocsHeaderComponentProps } from '../scripts/types';
import { useDarkMode } from '../scripts/hooks';

const ToggleDark: React.FC = () => {
  const [isDark, setDark] = useDarkMode();
  const toggleDark = React.useCallback(() => setDark((value) => !value), []);

  return (
    <div className="dm-toggle" onClick={toggleDark}>
      <div className="dm-toggle-track">
        <div className="dm-toggle-track-check">
          <img
            src="data:image/png;base64,"
            role="presentation"
            width="16"
            height="16"
          />
        </div>
        <div className="dm-toggle-track-x">
          <img
            src="data:image/png;base64,"
            role="presentation"
            width="16"
            height="16"
          />
        </div>
      </div>
      <div className="dm-toggle-thumb" />
      <input
        className="dm-toggle-sr-only"
        type="checkbox"
        aria-label="Switch between Dark and Light mode"
        checked={isDark}
        onChange={toggleDark}
      />
    </div>
  );
};

const Header: React.FC<PiralDocsHeaderComponentProps> = ({ title, search, logo, nav }) => {
  const [active, setActive] = React.useState(false);
  const version = useGlobalState((s) => s.docs.version);
  const updated = useGlobalState((s) => s.docs.updated);
  const toggleActive = React.useCallback(() => setActive((active) => !active), []);

  return (
    <header>
      <div className="layout-container header">
        <div className="logo">
          <Link to="/">{logo}</Link>
          <span className="brand-name">{title}</span>
          <span className="brand-suffix">Docs</span>
        </div>
        {search}
        <ToggleDark />
        <div className="hamburger">
          <a href="#" onClick={toggleActive}>
            <i className="fas fa-bars" />
          </a>
        </div>
        <div className="version-info">
          {version && (
            <div>
              <i className="fas fa-code-branch" />v{version}
            </div>
          )}
          {updated && (
            <div>
              <i className="far fa-clock" />
              Updated {updated}
            </div>
          )}
        </div>
      </div>
      <nav className="layout-container" data-open={active}>
        {nav}
      </nav>
    </header>
  );
};

export default Header;
